.movies{
    padding:62px 0px 71px 0px
}

.movies-list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;
    &__poster{
        position: relative;
    }
    &__company{
        position: absolute;
        font-family: "Poppins-Medium", sans-serif;
        font-weight: 500;
        color:#fff;
        text-transform: uppercase;
        font-size: 14px;
        padding:7px 5px;
        right:0px;
        top:10px;
    }
    &__company-netflix{
        background-color: #d72323;
    }
    &__company-amazon{
        background-color: #0534cf;
    }
    &__descr{
     margin-top: 19px;
     text-align: center;
     flex-grow: 1;

    }
    &__item{
        display: flex;
        flex-direction: column;
        height: 405px;
        width: 210px;
        background-color: #3a4750;
        margin-bottom: 30px;
       
    }
    &__name{
        display: block;
        font-family:'Poppins-Medium', sans-serif;
        font-weight: 500;
        color:#fff
    }
    &__age, &__type{
        font-family: 'Poppins-Regular', sans-serif;
        font-weight: 400;
        color: #d9d9d9;
        text-transform: uppercase;
        font-size: 14px;
    }
    &__ratings{
      padding-left: 0px;
      max-width: 100%;;
      padding-bottom: 20px;
      justify-content: center;
    
    }
    &__ratings .ratings__tomato::before{
        width: 20px;
        height: 20px;
        background-size: cover;
      
      }
      &__ratings .ratings__tomato .ratings__tomato-score{
          margin-left:27px;
          margin-right:15px;
      }
      &__ratings .ratings__popcorn .ratings__user-score{
        margin-left:21px;
    }
      &__ratings .ratings__popcorn::before{
        width: 15px;
        height: 20px;
        background-size: cover;
      }
      
}

.btn{
    padding-top: 13px;
    padding-bottom: 15px;
    background-color: #e73327;
    border-radius: 20px;
    text-align: center;
    max-width: 210px;
    margin: 0 auto;
    font-family:'Poppins-Medium', sans-serif;
    font-size: 18px;
    color:#fff;
    cursor: pointer;
}

@media(max-width:992px){
.btn{
    display:none}
}

@media(max-width:768px){
    .movies-list{
        justify-content: space-around;
       &__item{
           width: 251px;
           min-height:450px;
       }
    
       &__img{
           width: 100%;
           max-height:340px;
       }    
    }
}

@media(max-width:576px){
    .movies-list{
        justify-content: center;
        &__item{
           width: 270px;
        }
     
       
       
    }
}